const validationPindahBarang = (value) => {
    const errors = {}
    if (!value.kodeBarang) {
        errors.kodeBarang = "Tidak Boleh kosong"
    }
    if (!value.keterangan) {
        errors.keterangan = "Tidak Boleh kosong"
    }
    if (!value.kadar) {
        errors.kadar = "Tidak Boleh kosong"
    }
    if (!value.pkg) {
        errors.pkg = "Tidak Boleh kosong"
    }
    if (!value.bruto) {
        errors.bruto = "Tidak Boleh kosong"
    }
    if (!value.netto) {
        errors.netto = "Tidak Boleh kosong"
    }
    return errors
}

export default validationPindahBarang;